body, h1 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainArea {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__fields {
    display: flex;
    align-self: flex-start;
    width: 100%;
  }

  &__textField {
    padding: 12px;
    width: 30%;
    box-sizing: border-box;
    border: 1px solid #000;
    border-bottom: none;
  }

  &__textField--secondElement {
    border-left: none;
    width: 40%;
  }

  &__controls {
    align-self: start;
    display: flex;
    flex-direction: row;
    width: 60%;
    gap: 30px;
  }
}

.btn {
  &:focus {
    outline: none;
  }
}

#canvas {
  border: 1px solid #000;
}